import { useState, useEffect } from "react";
import "./index.css"
import { validation } from "./validation"
import { useNavigate, useParams } from "react-router-dom";
import Axios from '../../api/Axios';
import triggerSwalAlert from "../../helpers/triggerSwalAlert";
import Loader from "../../components/Loader";

const axios = new Axios();

const ResetPassword = () => {

    const { token } = useParams()

    const [pending, setPending] = useState(false)

    const navigator = useNavigate()

    const [error, setError] = useState({
        email: false,
        confirmPassword: false,
        message: ""
    });

    const [data, setData] = useState({
        email: "",
        newPassword: "",
        confirmPassword: ""
    });

    const onChange = (value, target) => {
        setData({
            ...data,
            [target]: value,
        });
        if (error.email === true || error.confirmPassword === true || error.newPassword === true) {
            setError({
                ...error,
                [target]: false
            })
        }
    };

    const handleResetPassword = async (e) => {
        e.preventDefault()
        const valid = validation(data)
        if (!(data.newPassword === data.confirmPassword)) {
            valid["confirmPassword"] = true
        }
        setError(valid)
        if (valid.email || valid.confirmPassword || valid.newPassword) {
            return
        }
        try {
            const options = {
                method: "POST",
                body: { ...data, token }
            }
            setPending(true)
            const result = await axios.callApi(`auth/resetPassword`, options)
            if (result.status == 200){
                setPending(false)
                navigator("/sign-in")
                triggerSwalAlert("Success", "Password has been reset", "success")
            }
            else{
                setPending(false)
                triggerSwalAlert("Failure", "Error in changing password", "error")
            }
        }
        catch (error) {
            console.error("Something went wrong")
        }
    }

    const verifyToken = async () => {
        try {
            const options = {
                method: "POST",
                body: { token }
            }
            const result = await axios.callApi(`auth/verifyForgetToken`, options)
            if (result.status == 200) {
                setData({ ...data, email: result.data.data.email })
            }
            else
                navigator("*")
        }
        catch (error) {
            console.error("Something went wrong")
        }
    }
    useEffect(() => {
        verifyToken()
    }, [])

    return (
        <>
        {pending ? <Loader/> : null}
        <div className='resetPassword-wrapper bg-dark d-flex align-item-center justify-content-center w-100'>
            <div className='resetPassword'>
                <h2 className='mb-3'>Reset Passowrd</h2>
                <form>
                    <div className='form-group mb-2'>
                        <label htmlFor="email" className='form-label'>Email Address</label>
                        <input disabled id='email' className={`form-control ${error.email && "errorInputField"}`} required onChange={e => onChange(e.target.value, 'email')} value={data.email} />
                        {error.email && <div className='error'>
                            {error.message ? "Please enter a valid email" : "Please enter your email"}
                        </div>}
                    </div>
                    <div className='form-group mb-2'>
                        <label htmlFor="new password" className='form-label'>New password</label>
                        <input type="password" id='new password' className={`form-control ${error.newPassword ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'newPassword')} />
                        {error.newPassword && <div className='error'>
                            Please enter a password
                        </div>}
                    </div>
                    <div className='form-group mb-2'>
                        <label htmlFor="confirm password" className='form-label'>Confirm new password</label>
                        <input type="password" id='confirm password' className={`form-control ${error.confirmPassword ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'confirmPassword')} />
                        {error.confirmPassword && <div className='error'>
                            Passwords do not match
                        </div>}
                    </div>
                    <button className='btn btn-dark w-100 mt-2' onClick={e => handleResetPassword(e)}>RESET PASSWORD</button>
                </form>
            </div>
        </div>
        </>
    )
}

export default ResetPassword
